.form {
  padding: 46px 0;
  position: relative;
}
.form:before {
  content: '';
  position: absolute;
  left: -4%;
  top: 0;
  right: -4%;
  margin: auto;
  box-shadow: 0px -10px 10px rgba(0, 0, 0, 0.05);
  width: 108%;
  height: 100px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  pointer-events: none;
  z-index: -1;
}
.form:after {
  content: '';
  position: absolute;
  left: -4%;
  bottom: 0;
  right: -4%;
  margin: auto;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.05);
  width: 108%;
  height: 100px;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  pointer-events: none;
  z-index: -1;
}
