.page .wysiwyg h2 {
  font-size: 40px;
}
.page .wysiwyg h3 {
  font-size: 30px;
}
.page .wysiwyg h4 {
  font-size: 25px;
}
.page .wysiwyg h5 {
  font-size: 20px;
}
.page .wysiwyg h6 {
  font-size: 15px;
}
.page .wysiwyg p,
.page .wysiwyg li {
  line-height: 1.8;
  font-size: 15px;
}
.page .wysiwyg p,
.page .wysiwyg ul,
.page .wysiwyg ol {
  margin: 20px 0;
}
.page .wysiwyg ul {
  list-style-type: none;
  padding: 0;
}
.page .wysiwyg ul.two-cols {
  column-count: 2;
}
.page .wysiwyg ul li {
  position: relative;
  padding-left: 41px;
}
.page .wysiwyg ul li:not(:last-of-type) {
  margin-bottom: 10px;
}
.page .wysiwyg ul li:before {
  content: '';
  position: absolute;
  left: 10px;
  top: 10px;
  width: 6px;
  height: 6px;
  background-color: #000;
  border-radius: 100px;
}
.page .wysiwyg ol {
  list-style-type: none;
  padding: 0;
  counter-reset: item;
}
.page .wysiwyg ol.two-cols {
  column-count: 2;
}
.page .wysiwyg ol li {
  counter-increment: item;
  position: relative;
  padding-left: 41px;
}
.page .wysiwyg ol li:not(:last-of-type) {
  margin-bottom: 10px;
}
.page .wysiwyg ol li:before {
  content: counter(item) ".";
  position: absolute;
  left: 8px;
  top: 0;
  font-weight: 700;
}
.page .wysiwyg a {
  text-decoration: underline;
}
.page .wysiwyg table td {
  border: 1px solid #000;
  padding: 10px;
}
